@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.dayPropertyItem {
  width: 30%;
  height: 100px;
  margin-bottom: 10%;
}

.dayPropertyItem p:first-child {
  font-size: 30px;
  font-style: bold;

  margin-bottom: 0;
}

.dayCard {
  height: 75vh;
  margin: 10vh 5vw;
  padding: 5vh;

  scroll-snap-align: center;
  scroll-snap-stop: always;

  display: flex;
  flex-direction: row;
  justify-content: space-around;

  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 60px;

  font-family: 'Montserrat', sans-serif;
  color: #fff;

  z-index: 5;
}

.card  {
  display: flex;
  flex-direction: column;
  width: 40%;
  padding: 60px;
  text-align: left;
}

.cardData {
  font-size: 20px;
}

.dayCard h1 {
font-size: 4rem;
letter-spacing: 0.5rem;
}

.dayCard h1, .dayTempMain {
  margin: 0;
  padding: 0;
  font-size: 50px;
}

.dayMainInfo {
  display: flex;
  flex-direction: row;
  grid-gap: 40px;
  gap: 40px;

  margin: auto 0;
}

.cardDescription {
  text-transform: uppercase;
  font-size: 15px;
}

.dayProperties {
  width: 60%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-self: center;

  margin: auto;
}

.videoTag {
 position: fixed;
 top: 50%;
 left: 50%;
 width: 100vw;
 height: 100vh;
 object-fit: cover;
 transform: translate(-50%, -50%);
 pointer-events: none;
 z-index: -99;
}

